<template>
  <div class="main">
    <img class="top-left" src="../images/img_logo-result.png"/>
    <img class="top-right rotate" id="tg" :src="tgImg" @click="clickToggle"/>
    <img class="center-img" src="../images/img_picture.png"/>
    <a target="view_window" href="https://a.app.qq.com/o/simple.jsp?pkgname=com.psyone.brainmusic">
    <img class="bottom" src="../images/img_openning-adv.png"/>
    </a>

      <img @click="toPlayingPage" class="btn" id="btnBegin" src="../images/btn_start.png"/>

    <InputName v-if="inputshow" style="position:fixed"/>
  </div>
</template>

<script>
import InputName from '@/components/inputname.vue'
import Tool from '@/components/tool.js'

  export default {
      name: 'frist_page',
      data(){
          return {
              toggle: true,
              on: require('../images/btn_music-on.png'),
              off: require('../images/btn_music-off.png'),
              inputshow: false //这个是马上选取座位的时候 那个框展示出来不的判断
          }
      },
      components:{
          InputName
      },
      computed: {
          tgImg(){
              if(this.toggle){
                  this.music(true)
                  return this.on
              }
              else{
                  this.music(false)
                  return this.off
              }
          }
      },
      methods:{
          toPlayingPage(){
             if( !this.isWeiXin() ){
                 try {
                     if (Tool.is_cosleep() && JSON.parse(this.callAppRouter('isLogin')).msg === '已登录') {  //判断是否app端口  判断用户是否登录 如果不是app或者没有登录，就让他输入名称吧
                         alert(this.callAppRouter('Login'))
                     } else {
                         this.inputshow = true
                     }
                 }catch(e){
                     alert(e)
                 }
                 //this.callAppRouter('Login')
             }else{  this.$router.push({path:'/playing/b'})  }
          },
          callAppRouter(method, params={}, callback){
              let req = {
                  'Method': method,
                  'Data': params
              }, cbName = `CB_${Date.now()}_${Math.ceil(Math.random() * 10)}`;
              req = JSON.stringify(req);
              window.Xinchao.Web[cbName] = (err, result) => {
                  if (callback) {
                      callback(err, result);
                  }
                  delete window.Xinchao.Web[cbName];
              };
              if (/(iPhone|iPad|iPod)/i.test(navigator.userAgent)) {
                  window.webkit.messageHandlers.XinchaoApp.postMessage({
                      req,
                      cbName
                  });
              } else {
                  XinchaoApp.callRouter(req, cbName);
              }
          },
          clickToggle(){

              if(this.toggle == false){
                  document.getElementById('tg').classList.add('rotate')
                  document.getElementById('myaudio').play()
                  console.log('开始音乐')
                  this.toggle = !this.toggle
              }else{
                  document.getElementById('tg').classList.remove('rotate')
                  document.getElementById('myaudio').pause()
                  console.log('暂停吧')
                  this.toggle = !this.toggle
              }

          },
          weixinSDK(url,bl){
              const getappid = new XMLHttpRequest();
              const that = this

              getappid.open('GET', url + '/web/v1/wechat/config?debug=1&url=' + encodeURIComponent( location.href.split('#')[0] ) )
              //alert( url + '/web/v1/wechat/config?debug=1&url=' + encodeURIComponent( location.href.split('#')[0] ) )
              //上面的 后面的那个url我傻了...  后面那个是填写你当前页面的东西的
              getappid.onreadystatechange = function(){
                  if(getappid.readyState === 4 && getappid.status === 200){
                      console.log('sdk ok')

                      let getSDK = JSON.parse( getappid.response )
                      console.log(getSDK.data.wechat_config)
                      getSDK.data.wechat_config.debug = false


                      //alert(    )


                      //我觉得是每次都要config把！
                      //localStorage.setItem('config',   JSON.stringify( getSDK.data.wechat_config )  )

                      //我觉得有可能是hash的问题
                      //alert('https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx632d4c99bd681cf3&redirect_uri=' + encodeURI( location.href.split('#')[0] ) +'&response_type=code&scope=snsapi_userinfo#wechart_redirect')

                      if(bl) window.location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx632d4c99bd681cf3&redirect_uri=' + encodeURI( location.href.split('#')[0] ) +'&response_type=code&scope=snsapi_userinfo#wechart_redirect'
                      if(!bl){
                          wx.config( getSDK.data.wechat_config )
                          that.weixinShare()
                      }
                      //window.location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx632d4c99bd681cf3&redirect_uri=' + encodeURI( location.href.split('#')[0] ) +'&response_type=code&scope=snsapi_userinfo#wechart_redirect'
                  }
              }
              getappid.send()
          },
          weatherCode(){
              const QRblock = /code=(.+?)&/
              const code = window.location.href.match(QRblock)
              //code = code.replace('code=','')
              //没有理由啊 怎么回事 获取到code=?asdasdasd的啊 ....我日
              //alert( code )
              if(code){  return code } else{
                  return false
              }
          },
          isWeiXin(){
              return /micromessenger/.test(navigator.userAgent.toLowerCase())
          },
          weixinInit(url){
              if( this.isWeiXin() ){
                  let code = this.weatherCode()
                  if(!code){  this.weixinSDK(url,true)  }
                  else{ this.weixinSDK(url,false);sessionStorage.setItem('code', code)  }
              }
          },
          weixinShare(){
              //console.log( localStorage.getItem('config') )
              //wx.config( JSON.parse(localStorage.getItem('config')) );
              console.log(this.return_url)
              wx.onMenuShareAppMessage({
                  title: '教师节活动标题',
                  desc: '这是描述',
                  link: this.return_url,
                  imgUrl: 'http://wx4.sinaimg.cn/mw690/006Zdy2vgy1frycou5uzsj302l02b74g.jpg'
                  //success: ()=> alert('分享成功')
              })
              wx.onMenuShareQQ({
                  title: '教师节活动标题',
                  desc: '这是描述',
                  link: this.return_url,
                  imgUrl: 'http://wx4.sinaimg.cn/mw690/006Zdy2vgy1frycou5uzsj302l02b74g.jpg'
                  //success: ()=> alert('分享成功')
              })
              wx.onMenuShareQZone({
                  title: '教师节活动标题',
                  desc: '这是描述',
                  link: this.return_url,
                  imgUrl: 'http://wx4.sinaimg.cn/mw690/006Zdy2vgy1frycou5uzsj302l02b74g.jpg'
                  //success: ()=> alert('分享成功')
              })
              wx.onMenuShareTimeline({
                  title: '教师节活动标题',
                  desc: '这是描述',
                  link: this.return_url,
                  imgUrl: 'http://wx4.sinaimg.cn/mw690/006Zdy2vgy1frycou5uzsj302l02b74g.jpg'
                  //success: ()=> alert('分享成功')
              })


              wx.error((res)=>{
                  alert(res)
              })
          },
          appShare(){
              const share_data  = {
                  type1:{
                      title: '教师节活动标题',
                      desc: '描述',
                      link: this.return_url,
                      imgUrl: 'http://wx4.sinaimg.cn/mw690/006Zdy2vgy1frycou5uzsj302l02b74g.jpg',
                      shareType: 1
                  },
                  type2:{
                      title: '你画的月饼有多圆？',
                      //desc: '你画的多少分？PK一下！',
                      link: this.return_url,
                      imgUrl: 'http://wx4.sinaimg.cn/mw690/006Zdy2vgy1frycou5uzsj302l02b74g.jpg',
                      shareType: 2
                  },
                  type3:{
                      imgUrl:'',//空出来 不然优先分享图片
                      shareType: 3
                  }
              }
              document.getElementById('share').value = JSON.stringify(share_data)
              document.getElementById('share2').value = JSON.stringify(share_data.type1)
              document.getElementById('share3').value = JSON.stringify(share_data.type2)
          },
          music(toggle){
             const audio = document.getElementById('myaudio')
             if(audio) toggle  ? audio.play() : audio.pause()  //还没有获取到dom
          },
          initMusic(){
              //这是为了解决safari不能自动播放的问题的 因为用户没有交换 所以我决定放在touchstart那 然后添加toggle是为了一开始用户还没有进行交换就关闭Music了
              //还有我把他放在全局组件那是为了后面交换触发音乐
              //console.log('touchstart')
              if(this.toggle) {
                  setTimeout(()=>{
                      const audio = document.getElementById('myaudio')
                      audio.play()
                  },1000)  //感觉加上1000ms，用户有概率接触屏幕两次 从而触发
              }
          },
          weixinMusicPlay(){
              document.addEventListener("WeixinJSBridgeReady", function () {
                  document.getElementById('myaudio').play();
                  //document.getElementById('video').play();
              }, false);
          }
      },
      mounted(){
         // setTimeout(this.initMusic(), 1000)
          //document.addEventListener('touchstart', this.initMusic, false)
          this.weixinMusicPlay()
          this.weixinInit('https://api.debug.psy-1.com')
          this.appShare()
      }
  }
</script>



<style lang="scss" scoped>
  .main {
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    //background: url(../images/img_bg.jpg); 全局好
    //background-size: 100% 100%;  全局大法
    .center-img{
      width: 100vw;
    }

    %same{
      position: absolute;
    }
    $height-padding: 1em;
    .top-left{
      @extend %same;
      top: $height-padding;
      left: 1em;
      height: 2em;
    }
    .top-right{
      @extend %same;
      width: 3em;
      top: $height-padding;
      right: 1em;
    }
    $bottom_height: 3em;
    $bottom_bottom: 1em;
    %transitionX{
      right: 50vw;
      transform: translateX(50%);
    }
    .bottom{
      @extend %same;
      @extend %transitionX;
      height: $bottom_height;
      bottom: $bottom_bottom;
    }
    .btn{
      @extend %same;
      @extend %transitionX;
      height: 2em;
      bottom: $bottom_height + $bottom_bottom;
    }


  }
</style>
<style>
  .rotate{
    animation: rotate 7s infinite linear;
  }
  @keyframes rotate{
    0%{
      transform: rotate(0deg);
      -webkit-transform: rotate(0deg);
      -moz-transform: rotate(0deg);
    }
    100%{
      transform: rotate(360deg);
      -webkit-transform: rotate(360deg);
      -moz-transform: rotate(360deg);
    }
  }

  </style>
